.pagination {
  font-size: max(1rem, 12px);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.pagination span, .pagination a {
  display: block;
  height: 24px;
  line-height: 23px;
  background-color: #111;
  border: 1px solid #fff;
  text-align: center;
  transition: color 0.3s;
  margin: 0 0.5rem;
}

/* ページ番号 */
.pagination .pager {
  width: 24px;
}

/* ホバー時 & 現在のページ */
.pagination a:hover,
.pagination .current {
  color: #111;
  border-color: #fff;
  background-color: #fff;
  opacity: 1;
}

/* 前へ */
.pagination a.prev {
  padding: 0 13px;
  margin-right: 0.875rem;
}

/* 次へ */
.pagination a.next {
  padding: 0 13px;
  margin-left: 0.875rem;
}

/* 最初へ */
/* 最後へ */
/* Page x / y */
.pagination span.page_num {
  display: none;
}

@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
.swiper__mv-container .swiper-slide-active .slide-img,
.swiper__mv-container .swiper-slide-duplicate-active .slide-img,
.swiper__mv-container .swiper-slide-prev .slide-img {
  animation: zoomUp 10s linear 0s 1 normal both;
}
.swiper__mv-container .slide-img {
  position: relative;
}
.swiper__mv-container .slide-img::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}
.swiper__mv-container .slide-img img {
  display: block;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: -1.3125rem;
}
@media screen and (min-width: 768px) {
  .swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
    bottom: -1.75rem;
  }
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 0.3125rem;
}

.swiper-pagination-bullet {
  background: #c4c4c4;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background: #f9f871;
}

@media screen and (min-width: 768px) {
